div.img-wrap {
  cursor: pointer;
  border-radius: 5px;
  transition: box-shadow .3s cubic-bezier(.25, .8, .25, 1);
  box-shadow: 0 2px 6px #0000001f, 0 2px 4px #0000003d;
}

div.img-wrap:hover {
  z-index: 1000;
  transform: scale(1.125);
  box-shadow: 0 7px 14px #00000040, 0 5px 5px #00000038;
}

div.img-wrap .image {
  opacity: 1;
  backface-visibility: hidden;
  border-radius: 5px;
  width: 100%;
  height: auto;
  transition: opacity .5s;
  display: block;
}

div.img-wrap div.text-wrap {
  opacity: 0;
  text-align: center;
  transition: opacity .5s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

div.img-wrap:hover .image {
  opacity: .3;
}

div.img-wrap:hover div.text-wrap {
  opacity: 1;
}

div.img-wrap div.text-wrap div.text-content {
  padding: 1rem;
  font-size: 1rem;
}

.ReactVirtualized__Masonry__innerScrollContainer {
  background: #eef6ff;
}
